
.App {
  margin-top: -22px;
  min-height: 100vh;
  background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
}

.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  

}

.search-bar{
  width:  50%;
  border: none;
  padding: 10px;
  box-shadow: 0px 10px 30px rgba(57,56,61,0.205);
  
}
h1{
  text-align: center;
}

.search-button{
  background: darkblue;
  border: none;
  padding: 10px 20px;
  color: white;
  box-shadow: 0px 10px 30px rgba(57,56,61,0.205);
}

.rec{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.msg{
  padding-left: 30px;
}
.header{
  padding-top: 0;
  background-color: darkblue;
  margin-top: 0;
  color: white;
  text-align: center;
  padding-top: 4px;
  padding-bottom: 1px;
}

@media only screen and (max-width:320px){
  h1{
    font-size: 23px;
    text-align: center;
}
}


