.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(70, 67, 67);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
    max-width: 42%;
}
.para{
    max-width: 85%;
}
.image{
    border-radius: 50%;
    width: 200px;
    height: 200px;
}


@media only screen and (max-width:320px){
    .recipe{
        max-width: 80%;
        min-width: 80%;
    }

    
}
@media only screen and (max-width:375px){
    .recipe{
        max-width: 80%;
        min-width: 80%;
    }
}
@media only screen and (max-width:425px){
    .recipe{
        max-width: 80%;
        min-width: 80%;
    }
}